import React from 'react'

export function ImageBorder() {
  return (
    <svg
      className='circle-border'
      width='230'
      height='230'
      viewBox='0 0 220 220'
      fill='white'
      xmlns='http://www.w3.org/2000/svg'
    >
      <circle
        cx='102'
        cy='102'
        r='100'
        stroke='$clr1'
        strokeWidth='2'
        strokeDasharray='12 6'
      />
    </svg>
  )
}
